import React, { useState, useEffect } from "react";
import Papa from "papaparse";

function App() {
  // The url is relative to the public folder
  const url = "./data/mlb-teams.csv";

  // useState to store the CSV file as an array of arrays (as parsed by papaparse)
  const [csvFile, setCsvFile] = useState([]);

  // parse the blob after the page has loaded (?)
  useEffect(() => {
    parseBlob(url);
  }, []);

  const parseBlob = async (url) => {
    // How to get a file or blob from an object URL?
    // https://stackoverflow.com/questions/11876175/how-to-get-a-file-or-blob-from-an-object-url
    const getBlobFromUrl = async (url) => {
      const response = await fetch(url).then((r) => r.blob());
      return response;
    };

    const blob = await getBlobFromUrl(url);

    // https://www.papaparse.com/
    Papa.parse(blob, {
      complete: function (results) {
        // store state
        setCsvFile(results.data);
      },
    });
  };

  // Render Methods

  if (csvFile.length === 0) {
    return <div>{}</div>;
  } else {
    return (
      <div className="container">
        <div className="row ">
          <div className="col s6">
            <div className="card">
              <div className="card-content center">{renderTable(csvFile)}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const renderTableRow = (row) => {
  return row.map((d, i) => (
    <td key={i}>
      <code>{d}</code>
    </td>
  ));
};

const renderTable = (csvFile) => {
  return (
    <table style={{ overflow: "auto", display: "block" }}>
      <thead>
        <tr>{renderTableHead(csvFile[0])}</tr>
      </thead>
      <tbody>
        {renderTableBody([...csvFile.slice(1, csvFile.length - 1)])}
      </tbody>
    </table>
  );
};

const renderTableHead = (firstRow) => {
  return firstRow.map((d, i) => (
    <th key={i}>
      <code>{d}</code>
    </th>
  ));
};

const renderTableBody = (csvFile) => {
  return csvFile.map((d, i) => <tr key={i}>{renderTableRow(d)}</tr>);
};

export default App;
